
import { useState } from "react";
import { BrowserRouter as Routes, Link, Route, Switch } from "react-router-dom";
import datos from '../Calendario.json';
import './Calendario.css';
import sinActividad from '../imagenes/bts-bt21.gif';

const Buscador = ({valor}) => {
  const imagen = 0;
    return(
        <div>      
            <div className="tabla"
              style={{
                border: "20px"
              }}
            >
              {datos.map((past, index) => (past.mes == valor && past.visible == true)?
              <div className="datos-tabla" key={index}>
                <table className="table table-warning table-striped table-hover table-bordered">
                  <thead>                  
                      <tr><th className="tipo">ubicacion</th>
                        <td key={index + 1} className="table-secondary">{past.ubicacion}</td>
                      </tr>
                      <tr><th className="tipo">fecha</th><td key={index + 2} className="table-secondary">{past.fecha}</td></tr>
                      <tr><th className="tipo">dia</th><td key={index + 3} className="table-secondary">{past.dia}</td></tr>
                      <tr><th className="tipo">hora</th><td key={index + 4} className="table-secondary">{past.hora}</td></tr>
                      <tr><th className="tipo">evento</th><td key={index + 5} className="table-secondary">{past.evento}</td></tr>
                      <tr><th className="tipo">ingreso</th><td key={index + 6} className="table-secondary">{past.ingreso}</td></tr>                    
                  </thead>            
                </table>
              </div>:(past.mes == valor)?<img className="imagen-sinactividad" src={sinActividad}/>:null)}
            </div>
        </div>
    )
}
export default function Calendario() {
    const [mes, setMes] = useState();
    return(
        <div className="header-calendary">
            <Routes>    
            <h1 className="title-calendary">Actividades 2022</h1>
            <div className="months">                                          
                        <Link to={'/buscador'}><button className="meses" onClick={() => setMes("enero")}>enero</button></Link>
                        <Link to={'/buscador'}><button className="meses" onClick={() => setMes("febrero")}>Febrero</button></Link>
                        <Link to={'/buscador'}><button className="meses" onClick={() => setMes("marzo")}>Marzo</button></Link>
                        <Link to={'/buscador'}><button className="meses" onClick={() => setMes("abril")}>Abril</button></Link>
                        <Link to={'/buscador'}><button className="meses" onClick={() => setMes("mayo")}>Mayo</button></Link>
                        <Link to={'/buscador'}><button className="meses" onClick={() => setMes("junio")}>Junio</button></Link>
                        <Link to={'/buscador'}><button className="meses" onClick={() => setMes("julio")}>Julio</button></Link>
                        <Link to={'/buscador'}><button className="meses" onClick={() => setMes("agosto")}>Agosto</button></Link>
                        <Link to={'/buscador'}><button className="meses" onClick={() => setMes("septiembre")}>Septiembre</button></Link>
                        <Link to={'/buscador'}><button className="meses" onClick={() => setMes("octubre")}>Octubre</button></Link>
                        <Link to={'/buscador'}><button className="meses" onClick={() => setMes("noviembre")}>Noviembre</button></Link>
                        <Link to={'/buscador'}><button className="meses" onClick={() => setMes("diciembre")}>Diciembre</button></Link>                    
            </div>

            <Switch>        
                <Route path={'/buscador'}><Buscador valor={mes}/></Route>
            </Switch>                        
                        </Routes>
        </div>
    );
}